@import"https://maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css";@import"https://fonts.googleapis.com/css?family=Open+Sans:400,300";.notify-box{z-index:2000 !important;display:block !important;position:absolute;left:50%;transform:translate(-50%);max-width:400px;width:100%;height:60px;margin:auto;text-transform:capitalize}.notify-box .alert{margin:10px 0px;padding:7px 20px;border-radius:5px;font-family:"Open Sans",sans-serif;font-size:.9rem;font-weight:300;letter-spacing:1px}.notify-box .alert:hover{cursor:pointer}.notify-box .alert:before{padding-right:12px}.notify-box .alert-info{color:#00529b;background-color:#bde5f8;border:1px solid #78caf1}.notify-box .alert-info:before{content:"";font-family:"FontAwesome"}.notify-box .alert-warn{color:#9f6000;background-color:#feefb3;border:1px solid #fddf67}.notify-box .alert-warn:before{content:"";font-family:"FontAwesome"}.notify-box .alert-error{color:#d8000c;background-color:#ffbaba;border:1px solid #ff6e6e}.notify-box .alert-error:before{content:"";font-family:"FontAwesome"}.notify-box .alert-success{color:#4f8a10;background-color:#dff2bf;border:1px solid #bfe57f}.notify-box .alert-success:before{content:"";font-family:"FontAwesome";font-size:1.27rem}